"use client";
import { useState, useEffect } from 'react';

type Client = {
  id: string;
  name: string;
  company: string;
  email: string;
};

export default function ClientsPage() {
  const [clients, setClients] = useState<Client[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [formData, setFormData] = useState({ name: '', company: '', email: '' });

  useEffect(() => {
    fetchClients();
  }, []);

  const fetchClients = async () => {
    try {
      const res = await fetch('http://localhost:3001/api/clients');
      const data = await res.json();
      setClients(data);
    } catch (error) {
      console.error('Failed to fetch clients', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const res = await fetch('http://localhost:3001/api/clients', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      });
      if (res.ok) {
        setFormData({ name: '', company: '', email: '' });
        fetchClients();
      }
    } catch (error) {
      console.error('Failed to create client', error);
    }
  };

  return (
    <div>
      <h2 className="text-3xl font-bold mb-8">Gest√£o de Clientes</h2>

      {/* Create Client Form */}
      <div className="bg-brand-darkgray p-6 rounded-2xl mb-12 border border-white/10">
        <h3 className="text-xl font-bold mb-4 text-brand-neon">Novo Cliente</h3>
        <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <input
            type="text"
            placeholder="Nome"
            value={formData.name}
            onChange={e => setFormData({ ...formData, name: e.target.value })}
            className="bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
            required
          />
          <input
            type="text"
            placeholder="Empresa"
            value={formData.company}
            onChange={e => setFormData({ ...formData, company: e.target.value })}
            className="bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
            required
          />
          <input
            type="email"
            placeholder="Email"
            value={formData.email}
            onChange={e => setFormData({ ...formData, email: e.target.value })}
            className="bg-black/50 border border-white/10 rounded-lg p-3 text-white focus:outline-none focus:border-brand-neon"
            required
          />
          <div className="md:col-span-3 flex justify-end">
            <button type="submit" className="bg-brand-neon text-black font-bold py-3 px-6 rounded-lg hover:shadow-glow transition-all">
              Cadastrar Cliente
            </button>
          </div>
        </form>
      </div>

      {/* Client List */}
      <div className="bg-brand-darkgray rounded-2xl border border-white/10 overflow-hidden">
        <table className="w-full text-left">
          <thead className="bg-black/40 text-brand-gray text-sm uppercase tracking-wider">
            <tr>
              <th className="p-4">Nome</th>
              <th className="p-4">Empresa</th>
              <th className="p-4">Email</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-white/5">
            {clients.map(client => (
              <tr key={client.id} className="hover:bg-white/5 transition-colors">
                <td className="p-4 font-medium text-white">{client.name}</td>
                <td className="p-4 text-brand-gray">{client.company}</td>
                <td className="p-4 text-brand-gray">{client.email}</td>
              </tr>
            ))}
            {clients.length === 0 && !isLoading && (
              <tr>
                <td colSpan={3} className="p-8 text-center text-brand-gray">Nenhum cliente cadastrado.</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}
